.header {
  background-image: url(../img/header-bg.png);
  background-size: cover;
  background-position: 85%;

  @include onTablet {
    background-position: 50%;
  }

  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }

  &__title {
    margin: 0;
    text-align: center;

    font-size: 36px;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 64px;
    }
  }

  &__bottom {
    padding-bottom: 88px;

    @include onTablet {
      padding-bottom: 130px;
    }
  }
}
